<template>
  <div>

    <v-progress-circular v-if="!entryHtml" indeterminate style="margin: 15px 0 0 130px" />
    <div v-if="!!errorMessage">
      <v-icon>mdi-account-alert</v-icon>
      {{errorMessage}}
    </div>
    <div v-html="entryHtml" style="max-height: 99vh; overflow: auto;" />

  </div>

</template>

<script>
  import axios from 'axios';

  export default {
    props: ['sourcePath'],

    data: () => ({
      canLoad: true,
      errorMessage: '',
      entryHtml: ''
    }),

    watch: {
      sourcePath(newVal) {
        this.loadIfNeeded();
      }
    },

    methods: {
      loadIfNeeded() {
        if (this.canLoad) {
          this.canLoad = false;

          axios({ method: 'GET', url: this.sourcePath })
                    .then(result => { this.entryHtml = result.data; },
                          error  => { this.canLoad = true;
                                      this.entryHtml = ' ';
                                      console.error(error);
                                      switch(error.response && error.response.status) {
                                        // TODO these are specific to one use case; and we probably want a more global error balloon framework anyhow
                                        case 401: this.errorMessage = 'You do not have access to read this entry.'; break;
                                        case 404: this.errorMessage = 'Entry not found. The table of contents is probably broken.'; break;
                                        default : this.errorMessage = 'Could not load entry: ' + error.toString(); break;
                                      }
                                    });
        }
      }
    },

    mounted() {
      this.loadIfNeeded();
    }
  }

</script>
